import * as React from 'react';
import { AppBar, Typography, Toolbar, Button, createStyles, WithStyles, Avatar, Fab, Icon, List, ListItem, ListItemAvatar, ListItemText, Divider, Menu, TextField, MenuItem, Select } from '@material-ui/core';
import { withStyles } from '@material-ui/styles';
import chef from '../static/chef.svg';
import aboutapp from '../static/aboutapp.svg';
import ReactSVG from 'react-svg'
import Lightbox from 'react-image-lightbox';
import 'react-image-lightbox/style.css';
import pencil from '../static/pencil.svg';
import foodie from '../static/foodie.png';
import prototype from '../static/prototype.svg';
import wip from '../static/wip.svg';
import finalprototype from '../static/finalprototype.svg';
import blueprint from '../static/blueprint.jpg';
import ArrowUpward from '@material-ui/icons/ArrowUpward';
import party from '../static/party.png';
import PeopleIcon from '@material-ui/icons/People';
// @ts-ignore
import pdf from '../static/IPM_47326_47872_48550_50054.pdf';
// @ts-ignore
import phase2 from '../static/IPM_stage_2_47326_47872_48550_50054.pdf';
// @ts-ignore
import phase3 from '../static/IPM_stage_3_47326_47872_48550_50054.pdf';
// @ts-ignore
import assignment from '../static/Assignment-1.pdf';
// @ts-ignore
import assignmnetsara from '../static/Good-and-bad-design.pdf';
// @ts-ignore
import assignementluis from '../static/GoodUIBadDesign.pdf';

const styles = () => createStyles({
    menuButtons: {
        color: 'white',
        marginRight: '30px'
    },
    svg: {
        width: '75vh',
        float: 'right',
        marginRight: '7%',
    },
    svgContent: {
        width: '300px',
        height: '300px',
        float: 'left',
        marginRight: '7%',
    },
    header: {
        width: '100%',
        height: '62vh',
        background: 'radial-gradient(circle at 50% -300%,#FF8401 90%,transparent 90.1%),radial-gradient(circle at 60% -300%,#ffae57 90%,transparent 90.1%)'
    },
    skewDiv: {
        position: 'relative',
        paddingTop: '60px',
        '&::before': {
            width: '100%',
            height: '180%',
            transform: 'skewY(-8deg)',
            background: '#f6f9fc',
            position: 'absolute',
            content: '""',
            zIndex: -1,
            top: 0
        }
    },
    logoImages: {
        width: '20%',
        cursor: 'pointer',
        marginRight: '25px'
    },
    pencilSvg: {
        width: '50px',
        height: '50px'
    },
    prototypeSvg: {
        width: '40px',
        height: '40px',
    },
    wip: {
        width: '20%'
    },
    prototypeBackground: {
        position: 'relative',
        height: '100%',
        marginTop: '90px',
        padding: '50px',
        zIndex: 1,
        backgroundImage: `url(${blueprint})`,
        backgroundPosition: 'center'
    },
    finalPrototype: {
        width: '40px',
        height: '40px',
        marginTop: '5px'
    },
})

export interface State {
    prevScrollpos: number;
    visible: boolean;
    photoIndex: number;
    isOpen: boolean;
    isLogoView: boolean;
    goUp: boolean;
    images: string[];
    protImages: string[];
    currentImages: string[]
}

type PropsAndStyles = WithStyles<typeof styles>;

export class Navbar extends React.Component<PropsAndStyles, State> {
    constructor(props: PropsAndStyles) {
        super(props)
        this.state = {
            prevScrollpos: window.pageYOffset,
            visible: true,
            photoIndex: 0,
            isOpen: false,
            isLogoView: false,
            goUp: false,
            images: [],
            protImages: [],
            currentImages: []
        }
    }

    componentDidMount() {
        window.addEventListener("scroll", this.isScrolledIntoView);
        window.addEventListener("scroll", this.showGoUp);
        this.imagesLogo();
        this.imagesPrototype();
    }

    componentWillUnmount() {
        window.removeEventListener("scroll", this.isScrolledIntoView);
        window.removeEventListener("scroll", this.showGoUp);
    }

    genImages = (images: string[]) => {
        return images.map((img, i) => <img src={img} className={this.props.classes.logoImages} onClick={this.openLightbox(i, images)} />)
    }

    imagesLogo = () => {
        const r = require.context('../static/logo', false, /\.(png|jpe?g|svg)$/);
        const newImg = r.keys().map(r);
        const finalArr = newImg.map((img) => img as string);
        this.setState({
            images: finalArr
        });
    }

    imagesPrototype = () => {
        const r = require.context('../static/firstprototype', false, /\.(png|jpe?g|svg)$/);
        const newImg = r.keys().map(r);
        const finalArr = newImg.map((img) => img as string);
        this.setState({
            protImages: finalArr
        });
    }
    /*handleScroll = () => {
        const { prevScrollpos } = this.state;

        const currentScrollPos = window.pageYOffset;
        const visible = prevScrollpos > currentScrollPos;

        this.setState({
            prevScrollpos: currentScrollPos,
            visible
        });
    }; */

    openLightbox = (index: number, images: string[]) => (event: React.MouseEvent<HTMLImageElement>) => {
        this.setState({
            isOpen: true,
            photoIndex: index,
            visible: false,
            currentImages: images
        })
    }

    showGoUp = () => {
        const el = document.getElementById('logo');
        if (el) {
            const rect = el.getBoundingClientRect();
            const elemTop = rect.top;

            console.log(rect.top, rect.bottom);
            this.setState({
                goUp: rect.bottom < 1400
            });
        }
    }

    isScrolledIntoView = () => {
        const el = document.getElementById('logo');
        let isVisible = false;
        if (el) {
            const rect = el.getBoundingClientRect();
            const elemTop = rect.top;
            const elemBottom = rect.bottom;

            isVisible = elemTop + 80 < window.innerHeight && elemBottom >= 0;

        }
        if (isVisible !== this.state.isLogoView) {
            this.setState({
                isLogoView: isVisible
            });
        }
    }

    jumpTo = (id: string) => (event: React.MouseEvent<HTMLButtonElement>) => {
        const el = document.getElementById(id);

        if (el) {
            el.scrollIntoView({ behavior: 'smooth' });
        }
    }

    render = () => {
        const { classes } = this.props;
        const { photoIndex, images } = this.state;
        return (

            <div id={'root'}>
                <AppBar style={{ background: 'transparent' }} elevation={0} position={'absolute'}>
                    <div style={{
                        width: '100%', background: 'transparent', borderBottom: '1px solid white', height: '30px', display: 'block',
                        textAlign: 'right'
                    }}>
                        <Typography variant={'caption'} style={{ margin: '5px 10px 5px 0' }}>Interação Pessoa Máquina 19/20</Typography>
                    </div>
                    <Toolbar style={{ padding: '10px' }}>
                        <img src={foodie} />
                        <div style={{ right: '20px', position: 'absolute' }}>
                            <Button className={classes.menuButtons} onClick={this.jumpTo('about')}>Sobre a App</Button>
                            <Button className={classes.menuButtons} onClick={this.jumpTo('logo')}>Logótipo</Button>
                            <Button className={classes.menuButtons} onClick={this.jumpTo('firstprototype')}>1º Protótipo</Button>
                            <Button className={classes.menuButtons} onClick={this.jumpTo('finalprototype')}>Protótipo Final</Button>
                            <Button className={classes.menuButtons} onClick={this.jumpTo('aboutus')}>Sobre Grupo</Button>
                        </div>
                    </Toolbar>
                </AppBar>
                <div style={{ height: '20vh', width: '100%', background: '#FF8401', display: 'flex', paddingTop: '50px' }}>
                    <div style={{ flexGrow: 1, marginTop: '10%', marginLeft: '5%' }}>
                        <Typography variant={'h2'} style={{ color: 'white', fontWeight: 'bolder' }}>Cozinhe os melhores pratos.</Typography>
                        <Typography variant={'h5'} style={{ color: 'white' }}>E conheça as melhores pessoas no caminho.</Typography>
                        <Button style={{ backgroundColor: 'white', borderRadius: '25px', marginTop: '30px' }}>Descobrir agora</Button>
                    </div>
                    <ReactSVG src={chef}
                        style={{ flexGrow: 1 }}
                        beforeInjection={svg => {
                            svg.classList.add(classes.svg)
                            svg.setAttribute('style', 'viewbox: 0 125 903.77 657.14')
                        }} />
                </div>
                <div className={classes.header} />
                <div style={{ padding: '100px' }} id={'about'}>
                    <Typography variant={'h4'}>Sobre a App</Typography>
                    <div style={{ display: 'flex' }}>
                        <ReactSVG src={aboutapp}
                            style={{ display: 'inline-block', flex: 0.45 }}
                            beforeInjection={svg => {
                                svg.classList.add(classes.svgContent)
                            }} />
                        <div style={{ display: 'inline-block', flex: 1, paddingTop: '35px' }}>{`A Foodie é uma aplicação feita por cozinheiros para cozinheiros,
                        idealizada para permitir não só que alguém que tenha interesse em cozinha possa conectar-se com outras pessoas, como também, procurar
                        as melhores receitas.
                        É possível interagir socialmente com outras pessoas através das receitas que colocam na aplicação, sendo que um utilizador pode
                        comentar, avaliar, reagir e experimentar uma receita, tal como colocar a sua própria receita para que outros possam interagir. Assim,
                        é possível tirar duvidas, dar dicas e tudo mais que for necessário.`}
                            <br /><br />
                            {` Outro aspeto incorporado nesta aplicação é a possibilidade de realizar compras de acordo com os ingredientes estipulados
                        pelo utilizador que colocou a receita, tal como, a realização de gestão dos ingredientes que o utilizador tem.`} </div>
                    </div>
                </div>
                <div className={classes.skewDiv} id={'logo'}>
                    <div style={{ position: 'relative', textAlign: 'center' }}>

                        <Avatar style={{ backgroundColor: '#fcc07e', width: '50px', height: '50px', margin: '0 auto', marginBottom: '15px' }}>
                            {this.state.isLogoView && <ReactSVG src={pencil}
                                beforeInjection={svg => {
                                    svg.classList.add(classes.pencilSvg)
                                }} />}
                        </Avatar>
                        <Typography variant={'h5'}>Evolução Logotipo</Typography>
                    </div>
                    <div style={{ padding: '25px 100px 0px 100px' }}>
                        <div style={{ marginBottom: '20px' }}>
                            {`A ideia para o nosso logotipo começou de volta do nome que tínhamos idealizado na altura:
                        CooKing, e, estudamos alguns logotipos possiveis. No entanto, apercebemos-nos que o nome "CooKing" não funcionava, não dizia nada
                        quanto à aplicação ser uma rede social para cozinheiros, e, transmitia mais a ideia de ser uma competição, ou de, focar-se unicamente
                        no desenvolvimento de skills de cozinha.`}
                            <br /><br />
                            {`Como tal, surgiu o nome Foodie, mais agradável, que remete para alguém que não só gosta de cozinhar, como também, gosta de comer.
                        Procurámos então refletir isso no logotipo. A ideia base foi um cozinheiro "guloso", e, através da utilização de um chapéu de chef, conseguimos
                        transmitir a ideia de que qualquer pessoa pode ser o chef da sua própria cozinha, e, através da boca com a lingua de fora, damos a ideia
                        que a aplicação é um sítio perfeito para encontrar receitas que deixam uma pessoa a salivar. `}
                        </div>
                        <div style={{ textAlign: 'center' }}>
                            {this.genImages(this.state.images)}
                        </div>
                    </div>

                    {this.state.isOpen && <Lightbox

                        mainSrc={this.state.currentImages[photoIndex]}
                        nextSrc={this.state.currentImages[(photoIndex + 1) % this.state.currentImages.length]}
                        prevSrc={this.state.currentImages[(photoIndex + this.state.currentImages.length - 1) % this.state.currentImages.length]}
                        onCloseRequest={() => this.setState({ isOpen: false, visible: true })}
                        onMovePrevRequest={() =>
                            this.setState({
                                photoIndex: (photoIndex + this.state.currentImages.length - 1) % this.state.currentImages.length,
                            })
                        }
                        onMoveNextRequest={() =>
                            this.setState({
                                photoIndex: (photoIndex + 1) % this.state.currentImages.length,
                            })
                        }
                    />}
                </div>
                <div className={classes.prototypeBackground} id={'firstprototype'}>
                    <div style={{ display: 'table' }}>
                        <Avatar style={{ backgroundColor: '#FF8401', width: '60px', height: '60px', marginRight: '20px' }}>
                            <ReactSVG src={prototype}
                                beforeInjection={svg => {
                                    svg.classList.add(classes.prototypeSvg)
                                }} />
                        </Avatar>
                        <Typography variant={'h4'} style={{ display: 'table-cell', verticalAlign: 'middle' }}>
                            Primeiro Protótipo
                            </Typography>
                    </div>
                    <div style={{ textAlign: 'center' }}>
                        {this.genImages(this.state.protImages)}
                    </div>
                </div>
                <div style={{ backgroundImage: `url(${party})`, backgroundSize: '90%', backgroundRepeat: 'no-repeat', zIndex: 1, height: '400px', padding: '50px' }}
                    id={'finalprototype'}>
                    <div style={{ display: 'table' }}>
                        <Avatar style={{ backgroundColor: '#FF8401', width: '60px', height: '60px', marginRight: '20px' }}>
                            <ReactSVG src={finalprototype}
                                beforeInjection={svg => {
                                    svg.classList.add(classes.finalPrototype)
                                }} />
                        </Avatar>
                        <Typography variant={'h4'} style={{ display: 'table-cell', verticalAlign: 'middle' }}>
                            Protótipo Final
                            </Typography>
                    </div>
                    <div style={{ textAlign: 'center', marginTop: '20px' }}>
                        <ReactSVG src={wip}
                            beforeInjection={svg => {
                                svg.classList.add(classes.wip)
                            }} />
                        <Typography>⚠️ Os protótipos são um trabalho em progresso. Por favor verifique mais tarde! ⚠️</Typography>
                    </div>
                </div>
                <div style={{ backgroundColor: '#FAFAFA', zIndex: 1, height: '400px', padding: '50px', paddingBottom: '200px' }}
                    id={'aboutus'}>
                    <div style={{ display: 'table' }}>
                        <Avatar style={{ backgroundColor: '#FF8401', width: '60px', height: '60px', marginRight: '20px' }}>
                            <PeopleIcon />
                        </Avatar>
                        <Typography variant={'h4'} style={{ display: 'table-cell', verticalAlign: 'middle' }}>
                            Sobre Grupo
                            </Typography>
                    </div>
                    <List>
                        <ListItem>
                            <ListItemAvatar>
                                <Avatar>R</Avatar>
                            </ListItemAvatar>
                            <ListItemText
                                primary={<b>Rúben Rebelo</b>}
                                secondary={<Typography>
                                    Nº 47326
                                </Typography>} />
                            <ListItem>
                                <Button href={assignment} variant={'outlined'} style={{ borderColor: '#FF8401', color: '#FF8401', marginLeft: '70px' }}>Assignment 1</Button>
                            </ListItem>
                        </ListItem>
                        <Divider variant="inset" component="li" />
                        <ListItem>
                            <ListItemAvatar>
                                <Avatar>S</Avatar>
                            </ListItemAvatar>
                            <ListItemText
                                primary={<b>Sara Fernandes</b>}
                                secondary={<Typography>
                                    Nº 47872
                                </Typography>} />
                            <ListItem>
                                <Button href={assignmnetsara} variant={'outlined'} style={{ borderColor: '#FF8401', color: '#FF8401', marginLeft: '70px' }}>Assignment 1</Button>
                            </ListItem>
                        </ListItem>
                        <Divider variant="inset" component="li" />
                        <ListItem>
                            <ListItemAvatar>
                                <Avatar>L</Avatar>
                            </ListItemAvatar>
                            <ListItemText
                                primary={<b>Luís Coelho</b>}
                                secondary={<Typography>
                                    Nº 48550
                                </Typography>} />
                            <ListItem>
                                <Button href={assignementluis} variant={'outlined'} style={{ borderColor: '#FF8401', color: '#FF8401', marginLeft: '70px' }}>Assignment 1</Button>
                            </ListItem>
                        </ListItem>
                        <Divider variant="inset" component="li" />
                        <ListItem>
                            <ListItemAvatar>
                                <Avatar>D</Avatar>
                            </ListItemAvatar>
                            <ListItemText
                                primary={<b>Diogo Escaleira</b>}
                                secondary={<Typography>
                                    Nº 50054
                                </Typography>} />
                        </ListItem>
                    </List>
                    <div style={{ display: 'flex', marginLeft: '20px' }}>
                        <Avatar style={{ backgroundColor: '#ffae57' }}>1ºR</Avatar><Typography style={{ flex: 1, marginTop: '5px', marginLeft: 10 }}>Primeiro Relatório:
                       </Typography>
                    </div>
                    <Button href={pdf} variant={'outlined'} style={{ borderColor: '#FF8401', color: '#FF8401', marginLeft: '70px' }}>Clicar aqui</Button>
                    <div style={{ marginTop: '50px' }}>
                    </div>
                    <div style={{ display: 'flex', marginLeft: '20px' }}>
                        <Avatar style={{ backgroundColor: '#c75b26' }}>2ºR</Avatar><Typography style={{ flex: 1, marginTop: '5px', marginLeft: 10 }}>Segundo Relatório:
                       </Typography>
                    </div>
                    <Button href={phase2} variant={'outlined'} style={{ borderColor: '#c75b26', color: '#c75b26', marginLeft: '70px' }}>Clicar aqui</Button>
                    <div style={{ marginTop: '50px' }}>
                    </div>
                    <div style={{ display: 'flex', marginLeft: '20px' }}>
                        <Avatar style={{ backgroundColor: '#FF8401' }}>3ºR</Avatar><Typography style={{ flex: 1, marginTop: '5px', marginLeft: 10 }}>Terceiro Relatório:
                       </Typography>
                    </div>
                    <Button href={phase3} variant={'outlined'} style={{ borderColor: '#FF8401', color: '#FF8401', marginLeft: '70px' }}>Clicar aqui</Button>
                    <div style={{ marginTop: '50px' }}>
                    </div>
                </div>
                {this.state.goUp && <Fab onClick={this.jumpTo('root')}
                    id={'scrollButton'} style={{ position: 'fixed', bottom: '20px', right: '20px', zIndex: 3 }}>
                    <ArrowUpward />
                </Fab>}
            </div>);
    }
}

export default withStyles(styles)(Navbar);